// for 表单排布

import React from 'react';
import classnames from 'classnames';

export default class FormItem extends React.Component {
  constructor(props) {
    super(props);

    this.state = {};
  }

  static defaultProps = {
    prefixCls: 'im-form-item'
  };

  static PropTypes = {
  };

  render() {
    const { className, prefixCls, ...others } = this.props;

    const allCls = {
      [className]: !!className,
      [prefixCls]: true
    };

    return (
      <div {...others} className={classnames(allCls)}>
        {this.props.children}
      </div>
    );
  }
}
